* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-image: url("innovation_background.04dcd5f5.jpg");
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border: none;
  border-radius: 2px;
  outline: .5px solid #000;
  margin-bottom: 16px;
  padding: 8px;
}

.request {
  letter-spacing: .03em;
  color: #2c2c2c;
  text-shadow: 0 5px 4px rgba(0, 0, 0, .25);
  margin: 10px 0;
  font-family: Concert One, cursive;
  font-size: 22px;
  font-weight: 400;
  line-height: 1.19;
  display: block;
}

#search-box:focus, #search-box:hover {
  background-color: #afecfc;
  border-radius: 2.25px;
  outline: .5px solid rgba(0, 0, 0, .75);
  box-shadow: 2px 4px 3px rgba(0, 0, 0, .25);
}

.country-list {
  margin: 0;
  padding: 0;
  font-family: Roboto;
  list-style: none;
  transition: transform .5s cubic-bezier(.4, -.16, .09, 1.51);
  transform: translateX(101%);
}

.country-list.translate {
  transform: translateX(0);
}

.country-item {
  letter-spacing: .5px;
  align-items: center;
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: 700;
  display: flex;
}

.country-item__link {
  justify-content: flex-start;
  align-items: center;
  margin: 0 0 20px;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.country-item:last-child {
  margin-bottom: 0;
}

.country-flag {
  max-width: 100%;
  height: auto;
  width: 60px;
  height: 40px;
  margin-right: 10px;
  display: block;
  box-shadow: 2px 4px 3px rgba(0, 0, 0, .25);
}

.country-flag--single {
  width: 120px;
  height: 80px;
}

.country-item--single {
  font-size: 30px;
}

.country-info {
  padding-top: 20px;
}

.country-description {
  margin: 0 0 10px;
  font-size: 20px;
}

.description-label {
  margin-right: 8px;
  font-weight: 700;
}

